/* header
 *
 */
header {
  background: #24292e;
  padding: 2rem 0;
}

header .container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

header .site-nav-toggle {
  color: #f5f6f7;
  font-size: 3.5rem;
  margin-left: -1rem;
  padding: .5rem 1rem .5rem 0;
  cursor: pointer;
}

