  color: white;
  font-size: 2rem;
  font-weight: 400;
}

@media (max-width: 991px) {
  header {
    top: 0;
    right: auto;
    bottom: auto;
    left: auto;
    position: fixed;
    padding: 0;
    transform: translateY(0);
    transition: transform 250ms ease-in-out;
    width: 100%;
    z-index: 101;
  }
  header.up {
    transform: translateY(-100%);
  }
  header .site-title {
    font-size: 1.5rem;
  }
}

/* layout
 *
 */
.main-outer {
  background: #f5f6f7;
  padding-top: 3rem;
  padding-bottom: 10rem;
}

.document > .section > h1, #search-results .document > .section > h2, #search-results .document > .section .toctree-wrapper .document > .section > .caption, .document > .section .toctree-wrapper #search-results .document > .section > .caption {
  margin-bottom: 5rem;
}

.document > .section {
  margin-bottom: 5rem;
}

.document > .section .toctree-wrapper {
  margin-top: 3rem;
}

/* site nav
 *
 */
.site-nav code.literal,
.site-nav tt.literal {
  border: none;
  background: inherit;
  color: inherit;
  padding-left: 0;
  padding-right: 0;
}

.site-nav-tree .caption {
  font-weight: bold;
}

.site-nav-tree ul {
  list-style-type: none;
  padding-left: 0;
}

.site-nav-tree ul ul {
  margin-left: 1.5rem;
}

.site-nav-tree li {
  line-height: 1.8;
}

.site-nav-tree > ul > li:not(.current) > ul {
  display: none;
}

.site-nav-tree a {
  color: rgba(0, 0, 0, 0.54);
}

.site-nav-tree a.current, .site-nav-tree a:hover {
  color: rgba(0, 0, 0, 0.87);
}

@media (max-width: 991px) {
  body {
    padding-top: 6.6rem;
  }
  .main-outer {
    position: relative;
  }
  .site-nav {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    background: #f5f6f7;
    height: 100vh;
    overflow-y: scroll;
    padding-top: 10rem;
    padding-bottom: 5rem;
    transform: translateX(-100%);
    transition: transform 250ms linear;
    z-index: 100;
  }
  body.nav-in {
    overflow: hidden;
  }
  body.nav-in .site-nav {
    transform: translateX(0);
  }
  body.nav-in .site-nav .icon-input {
    width: 100%;
  }
}

